header.main-header {
    display: grid;
    min-height: 100vh;
    grid-template-columns: minmax(auto, 30%) 1fr;
    grid-template-rows: rem(234px) 1fr auto;

    grid-template-areas:
        "logo slide"
        "case slide"
        "nav nav";

    .main-header-image {
        position: absolute;
        inset: 0;
        height: 100%;
        z-index: -1;
        object-fit: cover;
    }

    .main-header-logo {
        grid-area: logo;
        justify-self: start;
        width: rem(174px);
        padding: 22px;
        margin: 60px 60px 0;
        background-color: $clr3;
        color: $clr2;
        display: flex;
        align-items: flex-end;
    }

    .main-header-nav {
        display: flex;
        justify-self: end;
        grid-area: nav;
        top: 0px;
        right: 0px;
        z-index: 10;

        .main-nav {
            a {
                @include btn($type: light, $padding: em(42px) em(80px));
                display: block;
                &.active {
                    background-color: $clr1;
                    color: $clr3;
                }
            }
        }
        .main-header-search {
            svg {
                width: 20px;
            }
        }
    }

    .main-header-case {
        grid-area: case;
        justify-self: end;
        align-self: center;
        width: 320px;
        background-color: $clr2;
        text-transform: uppercase;
        display: flex;
        flex-direction: column;
        align-items: center;
        text-align: center;
        padding: 0 em(48px);
        margin-inline-start: 60px;

        &::before {
            content: "";
            width: 10px;
            height: 42px;
            background-color: $clr1;
            margin-bottom: em(42px);
        }

        .case-title {
            font-size: em(26px);
            line-height: 1.23;
        }

        .case-city {
            @include type(boldCondensed);
            color: $clr4;
            margin-bottom: em(44px);
        }
    }

    .main-header-slide-info {
        grid-area: slide;
        justify-self: end;
        max-height: 670px;
        background-color: $clr2;
        display: flex;
        flex-direction: column;
        align-items: center;
        padding-top: em(60px);
        text-transform: uppercase;
        @include type(boldCondensed);

        .slide-pager {
            flex: 1;
            margin-bottom: em(30px);
        }

        .slide-pager-controler {
            display: flex;

            button {
                padding: em(30px) em(23px);
            }

            svg {
                width: 6px;
            }
        }
    }
}
